/* fullscreen animated gradient background */
.login-wrap {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: radial-gradient(ellipse at bottom, #1b0b0b 0%, #121212 70%);
  overflow: hidden;
  position: relative;
}

/* moving mesh pattern */
.bg-pattern {
  position: absolute;
  inset: -100%;
  background-image:
    radial-gradient(circle at 20% 80%, rgba(230, 0, 35, .3) 0%, transparent 40%),
    radial-gradient(circle at 80% 30%, rgba(230, 0, 35, .25) 0%, transparent 35%),
    radial-gradient(circle at 50% 50%, rgba(230, 0, 35, .15) 0%, transparent 30%);
  animation: move 18s ease-in-out infinite;
  pointer-events: none;
}

@keyframes move {
  0%   { transform: translate(0, 0) rotate(0deg); }
  50%  { transform: translate(-4%, 4%) rotate(3deg); }
  100% { transform: translate(0, 0) rotate(0deg); }
}

/* glass card */
.login-card {
  position: relative;
  background: rgba(30, 30, 30, .55);
  backdrop-filter: blur(12px);
  border: 1px solid rgba(255, 255, 255, .08);
  border-radius: 20px;
  padding: 48px 64px;
  text-align: center;
  color: #fff;
  box-shadow: 0 20px 40px rgba(0, 0, 0, .4);
}

.logo {
  font-size: 3rem;
  margin: 0 0 8px;
  color: #e60023;
  letter-spacing: -1px;
}

.tagline {
  margin: 0 0 32px;
  opacity: .7;
}

/* pulsing red button */
.login-btn {
  display: inline-block;
  background: #e60023;
  color: #fff;
  padding: 14px 40px;
  border-radius: 500px;
  font-size: 1.1rem;
  font-weight: 600;
  text-decoration: none;
  transition: transform .2s, box-shadow .2s;
}

.login-btn:hover {
  transform: scale(1.05);
  box-shadow: 0 0 20px rgba(230, 0, 35, .6);
}